export function fetchData(): Promise<[Response]> {
    return fetch("https://backend-dashboard-react.herokuapp.com/task").then(
        response => response.json(),
    );
}

export function sendData(message: string): Promise<Response> {
    return fetch("https://backend-dashboard-react.herokuapp.com/user/task", {
        method: "POST",
        headers: {
            "Content-Type": "application/json",
        },
        body: JSON.stringify({ message }),
    });
}

export async function getUserTasks(email: string): Promise<Response> {
    return fetch("https://backend-dashboard-react.herokuapp.com/user/task", {
        method: "POST",
        headers: {
            "Content-Type": "application/json",
        },
        body: JSON.stringify({ email }),
    });
}

export function removeData(id: string): Promise<Response> {
    return fetch("https://backend-dashboard-react.herokuapp.com/task", {
        method: "DELETE",
        headers: {
            "Content-Type": "application/json",
        },
        body: JSON.stringify({ id }),
    });
}
