import { useState, useEffect, ReactElement, useContext } from "react";
import styles from "./styles.module.scss";
import { fetchData, removeData, sendData } from "./services";
import { AuthContext } from "../../contexts/auth";

export function Tasks(): ReactElement {
    const [newData, setNewData] = useState(false);
    const [dataFetch, setDataFetch] = useState([]);
    const [taskInput, setTaskInput] = useState("");

    const [loaded, setLoaded] = useState(false);

    const context = useContext(AuthContext);

    useEffect(() => {
        async function api<T>(url: string, email: string): Promise<T> {
            const response = await fetch(url, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify({ email }),
            });
            if (!response.ok) {
                throw new Error(response.statusText);
            }
            setLoaded(true);
            return response.json();
        }
        api(
            "https://backend-dashboard-react.herokuapp.com/user/task",
            context.user.user.email,
        )
            .then(data => setDataFetch([data]))
            .catch(error => {
                console.error(error);
            });

        // Dá Fetch nos items
    }, [newData]);

    useEffect(() => {
        // Dá um timeout

        if (newData) {
            setTimeout(() => {
                setNewData(false);
            }, 10);
        }
    }, [newData]);

    function handleSubmit(e: React.SyntheticEvent): void {
        e.preventDefault();

        async function api<T>(
            url: string,
            email: string,
            message: string,
        ): Promise<T> {
            const response = await fetch(url, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify({ email, message }),
            });
            if (!response.ok) {
                throw new Error(response.statusText);
            }
            setLoaded(true);
            return response.json();
        }
        api(
            "https://backend-dashboard-react.herokuapp.com/task",
            context.user.user.email,
            taskInput,
        ).then(() => {
            setTaskInput("");
            setNewData(true);
        });
    }

    function NumberTask(): ReactElement {
        const [data] = dataFetch;
        if (data.length === 0) {
            return <p className={styles.taskNumber}>Você não tem tarefas</p>;
        }
        if (data.length === 1) {
            return <p className={styles.taskNumber}>Você tem 1 tarefa</p>;
        }
        return (
            <p className={styles.taskNumber}>Você tem {data.length} tarefas</p>
        );
    }

    function taskRemove(item): void {
        removeData(item).then(() => setNewData(true));
    }

    function renderTasks(): ReactElement {
        const [data] = dataFetch;
        if (loaded && data !== undefined) {
            return (
                <div className={styles.geralContainer}>
                    <h1>Suas Tarefas</h1>

                    <div className={styles.divForm}>
                        <form onSubmit={e => handleSubmit(e)}>
                            <input
                                type="text"
                                id="taskInput"
                                placeholder="Insira uma tarefa"
                                onChange={event =>
                                    setTaskInput(event.target.value)
                                }
                                value={taskInput}
                            />
                            <button type="submit" id="buttonTask">
                                Adicionar
                            </button>
                        </form>
                        {NumberTask()}
                        <ul>
                            {data.map(item => (
                                <div>
                                    <li key={item.id}>{item.task}</li>

                                    <span
                                        role="button"
                                        onClick={() => taskRemove(item.id)}
                                        tabIndex={Number("0")}
                                    >
                                        X
                                    </span>
                                </div>
                            ))}
                        </ul>
                    </div>
                </div>
            );
        }
        return <h1>Carregando...</h1>;
    }

    return <div>{renderTasks()}</div>;
}
